  max-width: 320px;
  border: 2px solid #000;
  border-radius: 15px;
  margin-bottom: 24px;
  margin-left: 40px;
  padding: 10px;
  font-size: 24px;
  font-weight: 500;
  transition: transform .3s;
  box-shadow: 0 5px 10px rgba(154, 160, 185, .05), 0 15px 40px rgba(0, 0, 0, .1);
