#search-box:hover, #search-box:focus {
  border-color: #00f;
  outline: none;
  transform: scale(1.1);
}

.country-list {
  list-style: none;
}

.country-list li {
  padding-bottom: 10px;
  font-size: 22px;
  font-weight: 500;
  display: flex;
}

.country-list img {
  margin-right: 10px;
}

.country-info {
  margin-left: 50px;
}

.country-info img {
  margin-right: 10px;
